@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 500px) {
  
  html {
    font-size: 70%;
  }

}
body {
    font-family: 'Open Sans', sans-serif;
}
.weather-showcase {
    height: 100vh;
    background: url(/Weather-App/static/media/rain-showcase.d85a5067.jpg) center center/cover no-repeat;
    box-shadow: inset 0px 0px 0px 2000px rgba(0, 0, 0, 0.6);
    overflow: auto;
}
.weather-search {
    max-width: 800px;
    margin: 0 auto;
    padding: 3rem 0;
}

.weather-search__bar {
    display: flex;
    align-items: center;
    justify-content: center;
}

::-webkit-input-placeholder {
    color: #f4f4f4;
}

:-ms-input-placeholder {
    color: #f4f4f4;
}

::placeholder {
    color: #f4f4f4;
}

.weather-search__input {
    color: #fff;
    height: 3rem;
    width: 60%;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 2px solid #fff;
    font-size: 1.7rem;
    padding: 0.5rem 1rem;
}

.weather-search__btn {
    cursor: pointer;
    margin-left: 2rem;
    color: #fff;
    border: 2px solid #fff;
    padding: 1.2rem;
    border-radius: 50%;
    font-size: 1.7rem !important;
    transition: all 0.3s ease-out;
}

.weather-search__btn:hover {
    background: #fff;
    color: #333;
}

@media screen and (max-width: 500px) {
    
    .weather-search {
        padding: 2rem 0;
        padding-bottom: 0;
    }

}
.weather-display {
    max-width: 1400px;
    margin: 0 auto;
    color: #fff;
    display: grid;
    grid-template-columns: 2fr 1fr;
    margin-top: 10rem;
}

.weather-display__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.weather-display__city {
    display: flex;
    align-items: center;
    opacity: 0.8;
}

.weather-display__city p {
    font-size: 1rem;
    margin-left: 1rem;
}

.weather-display__details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 2px solid #fff;
    padding: 2rem;
}

.weather-display__list {
    list-style: none;
}

.weather-display__list li {
    font-size: 1.2rem;
    padding-bottom: 0.5rem;
    margin-bottom: 1rem;
    border-bottom: 2px solid #fff;
    font-weight: 600;
}

h1 {
    font-size: 10rem;
    margin: 0;
}

h3 {
    font-size: 2rem;
    margin-bottom: 2rem;
}

p {
    margin: 0;
}

@media screen and (max-width: 500px) {
    
    .weather-display {
        max-width: 400px;
        grid-template-columns: 1fr;
        padding: 0 2rem;
        margin-top: 1rem;
    }

    .weather-display__main {
        margin-bottom: 4rem;
    }

    .weather-display__details {
        margin-bottom: 2rem;
    } 

    h1 {
        font-size: 7rem;
    }

}

@media screen and (max-width: 1000px) {

    .weather-display {
        max-width: 750px;
        grid-template-columns: 1fr;
        padding: 0 1.5rem;
        margin-top: 1rem;
    }

    .weather-display__main {
        margin-bottom: 4rem;
    }

    .weather-display__details {
        margin-bottom: 2rem;
    } 

    h1 {
        font-size: 7rem;
    }


}
.weather-icon {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

p {
    font-size: 1.5rem;
}

img {
    margin-left: 1rem;
}
